import { useEffect } from "react";
import "./ConfirmOrder.css";
import { Typography } from "@material-ui/core";
import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { userAuth } from "../../../util/userAuth";
import Button from "../../ReusableComponent/Button/Button";
import Loader from "../../layout/Loader/Loader";
import MetaData from "../../layout/MetaData/MetaData";
import CheckOutSteps from "../CheckOutSteps/CheckOutSteps";

const ConfirmOrder = () => {
  const { shippingInfo, cartItems } = useSelector((state) => state.cart);
  const { user, isAuthenticated ,loading } = useSelector((state) => state.user);
  const navigate = useNavigate();
  const subtotal = cartItems.reduce(
    (prev, nex) => prev + nex.price * nex.quantity,
    0
  );

  const shippingCharges = subtotal > 1000 ? 0 : 200;
  const tax = subtotal * 0.18;
  const totalPrice = subtotal + shippingCharges + tax;
  const address =
    shippingInfo.address +
    ", " +
    shippingInfo.city +
    " - " +
    shippingInfo.pincode +
    ", " +
    shippingInfo.state +
    ", " +
    shippingInfo.country;

  const proceedToPayment = () => {
    const data = {
      subtotal,
      shippingCharges,
      tax,
      totalPrice,
    };
    sessionStorage.setItem("orderInfo", JSON.stringify(data));
    navigate("/process/payment");
  };

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
  }, [isAuthenticated, navigate,user]);

  return (
   
    loading ? <Loader /> : (
      <div className="confirmorder">
        <MetaData title="Confirm Order" />
        <CheckOutSteps activeStep={1} />
        <div className="confirmOrderPage">
          <div className="confirmCartItems">
            <Typography>Your Cart Items:</Typography>
            <div className="confirmCartItemsContainer">
              {cartItems.length > 0 &&
                cartItems.map((item) => (
                  <div key={item.product}>
                    <Link to={`/product/${item.product}`}>
                      <img src={`/${item.image}`} alt={item.name} />
                      {item.name}
                    </Link>
                    <span>
                      {item.quantity} X {item.price} =
                      <b>{item.quantity * item.price}</b>
                    </span>
                  </div>
                ))}
            </div>
          </div>
          <div className="confirmShippingArea">
            <Typography>Shipping Info</Typography>
            <div className="confirmShippingAreaBox">
              <div>
                <p>Name:</p>
                <span>{user.name}</span>
              </div>
              <div>
                <p>Phone:</p>
                <span>{shippingInfo.phoneNo}</span>
              </div>
              <div className="address">
                <p>Address:</p>
                <span>{address}</span>
              </div>
            </div>
          </div>
          <div className="orderSummary">
            <Typography>Order Summary</Typography>
            <div>
              <div>
                <p>Subtotal:</p>
                <span>₹{subtotal}</span>
              </div>
              <div>
                <p>Shipping Charges:</p>
                <span>₹{shippingCharges}</span>
              </div>
              <div>
                <p>GST:</p>
                <span>₹{tax}</span>
              </div>
              <div className="orderSummaryTotal">
                <p>
                  <b>Total:</b>
                </p>
                <span className="totalPrice">₹{totalPrice}</span>
              </div>
              <div>
                <Button onClick={proceedToPayment} className="proceedToPay">
                  Proceed To Payment
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  );
};

export default ConfirmOrder;
